import React from "react";

function Avatar(props) {
  const { id, image, isOnline } = props;
  return (
    <div className="avatar" >
      <div className="avatar-img">
        <img src={image} alt="#" />
      </div>
      <span className={`isOnline ${isOnline}`}></span>
    </div>
  );
}

export default Avatar;
